angular.module('jmCircleProgressModule').run(['$templateCache', function($templateCache) {$templateCache.put('app/cpCircleProgress/cpCircleProgress.html','<style>\n  @import url(https://fonts.googleapis.com/css?family=Lato:100,400);\n  @-webkit-keyframes load-jm-cp {\n    0% {\n      stroke-dashoffset: 0;\n    }\n  }\n  @keyframes load-jm-cp {\n    0% {\n      stroke-dashoffset: 0;\n    }\n  }\n  .jm-cp-progress {\n    position: relative;\n    display: inline-block;\n    padding: 0;\n    text-align: center;\n  }\n  .jm-cp-progress > li {\n    display: inline-block;\n    position: relative;\n    text-align: center;\n    color: #93A2AC;\n    font-family: Lato;\n    font-weight: 100;\n    margin: 2rem;\n  }\n  .jm-cp-progress > li:before {\n    content: attr(data-name);\n    position: absolute;\n    width: 100%;\n    bottom: -2rem;\n    font-weight: 400;\n  }\n  .jm-cp-progress > li:after {\n    content: attr(data-percent);\n    position: absolute;\n    width: 100%;\n    top: 3.7rem;\n    left: 0;\n    font-size: 2rem;\n    text-align: center;\n  }\n  .jm-cp-progress svg {\n    width: 10rem;\n    height: 10rem;\n  }\n  .jm-cp-progress svg:nth-child(2) {\n    position: absolute;\n    left: 0;\n    top: 0;\n    -webkit-transform: rotate(-90deg);\n    transform: rotate(-90deg);\n  }\n  .jm-cp-progress svg:nth-child(2) path {\n    fill: none;\n    stroke-width: 25;\n    stroke-dasharray: 629;\n    stroke: #fff;\n    opacity: .9;\n    animation: load-jm-cp 0.5s;\n    -moz-animation: load-jm-cp 0.5s;\n    -webkit-animation: load-jm-cp 0.5s;\n    transition: 0.5s;\n    -moz-transition: 0.5s;\n    -webkit-transition: 0.5s;\n  }\n</style>\n\n<!--  Container  -->\n<ul class="jm-cp-progress">\n  <!--  Item  -->\n  <li data-name="{{ctrl.description}}" data-percent="{{ctrl.percent}}%">\n    <svg viewBox="-10 -10 220 220">\n      <g fill="none" stroke-width="12" transform="translate(100,100)">\n        <path d="M 0,-100 A 100,100 0 0,1 86.6,-50" stroke="url(#cl1)"/>\n        <path d="M 86.6,-50 A 100,100 0 0,1 86.6,50" stroke="url(#cl2)"/>\n        <path d="M 86.6,50 A 100,100 0 0,1 0,100" stroke="url(#cl3)"/>\n        <path d="M 0,100 A 100,100 0 0,1 -86.6,50" stroke="url(#cl4)"/>\n        <path d="M -86.6,50 A 100,100 0 0,1 -86.6,-50" stroke="url(#cl5)"/>\n        <path d="M -86.6,-50 A 100,100 0 0,1 0,-100" stroke="url(#cl6)"/>\n      </g>\n    </svg>\n    <svg viewBox="-10 -10 220 220">\n      <path d="M200,100 C200,44.771525 155.228475,0 100,0 C44.771525,0 0,44.771525 0,100 C0,155.228475 44.771525,200 100,200 C155.228475,200 200,155.228475 200,100 Z" stroke-dashoffset="{{ctrl.strokeDashOffset}}"></path>\n    </svg>\n  </li>\n</ul>\n<!--  Defining Angle Gradient Colors  -->\n<svg width="0" height="0">\n  <defs>\n    <linearGradient id="cl1" gradientUnits="objectBoundingBox" x1="0" y1="0" x2="1" y2="1">\n      <stop stop-color="#618099"/>\n      <stop offset="100%" stop-color="#8e6677"/>\n    </linearGradient>\n    <linearGradient id="cl2" gradientUnits="objectBoundingBox" x1="0" y1="0" x2="0" y2="1">\n      <stop stop-color="#8e6677"/>\n      <stop offset="100%" stop-color="#9b5e67"/>\n    </linearGradient>\n    <linearGradient id="cl3" gradientUnits="objectBoundingBox" x1="1" y1="0" x2="0" y2="1">\n      <stop stop-color="#9b5e67"/>\n      <stop offset="100%" stop-color="#9c787a"/>\n    </linearGradient>\n    <linearGradient id="cl4" gradientUnits="objectBoundingBox" x1="1" y1="1" x2="0" y2="0">\n      <stop stop-color="#9c787a"/>\n      <stop offset="100%" stop-color="#817a94"/>\n    </linearGradient>\n    <linearGradient id="cl5" gradientUnits="objectBoundingBox" x1="0" y1="1" x2="0" y2="0">\n      <stop stop-color="#817a94"/>\n      <stop offset="100%" stop-color="#498a98"/>\n    </linearGradient>\n    <linearGradient id="cl6" gradientUnits="objectBoundingBox" x1="0" y1="1" x2="1" y2="0">\n      <stop stop-color="#498a98"/>\n      <stop offset="100%" stop-color="#618099"/>\n    </linearGradient>\n  </defs>\n</svg>');}]);